
import Group from 'vux/src/components/group/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import PlateType from '../../../js/enum.js'
import { mapState } from 'vuex'
import { objToString, isArrayType, getModuleKeyword, getImageUrl } from "../../../js/utility.js"
import cell from '../components/DynamicListCell.vue'
import $ from 'jquery'

export default {
name: 'dynamic',
components: {
Tab,
TabItem,
cell,
Group
},
computed: {
...mapState([
'dynamicList',
'userInfo',
]),
PlateType() {
return PlateType;
},
},
data() {
return {
nav_type: PlateType.Leads,
nav_list: [], //{ title: '全部', type: 'all' },
p: 1,
list: [],
canClick: true, //默认可以点击 点击后1秒后可重复点击
mDynamicList: {},
}
},
mounted() {

if (this.userInfo.admin === 1) {
this.nav_list = [{ title: '线索', type: PlateType.Leads }, { title: '客户', type: PlateType.Customer }, { title: '商机', type: PlateType.Business }, { title: '合同', type: PlateType.Contract }];
} else {
if (this.userInfo.leads) {
this.nav_list.push({ title: '线索', type: PlateType.Leads });
}
if (this.userInfo.customer) {
this.nav_list.push({ title: '客户', type: PlateType.Customer });
}
if (this.userInfo.business) {
this.nav_list.push({ title: '商机', type: PlateType.Business });
}
if (this.userInfo.contract) {
this.nav_list.push({ title: '合同', type: PlateType.Contract });
}
}

if (this.nav_list.length > 0) {
this.nav_type = this.nav_list[0].type;
this.mDynamicList = this.dynamicList;
if (typeof this.mDynamicList['nowtype'] !== "undefined") {
this.nav_type = this.mDynamicList.nowtype;
}
this.handelCache(this.nav_type);
}
},
beforeRouteLeave(to, from, next) {
if (typeof this.mDynamicList !== "undefined") {
this.mDynamicList['nowtype'] = this.nav_type; 
this.$store.commit('updateDynamicList', this.mDynamicList);
}
this.$store.commit('updateCrmDetailInfo', {});
next();
},
methods: {
clickNav(type, index) {
if (!$("#navtab").is(":animated") && this.canClick && (this.nav_type !== type)) {
this.canClick = false;
var self = this;
setTimeout(() => {
self.canClick = true;
}, 500); //点击速度过快，动画容易出错

this.saveCache();
this.handelCache(type);

if (document.body.clientWidth / 2 < 106 * (index + 0.5)) {
let offset = (106 * (index + 1) - 53) - document.body.clientWidth / 2;
$("#navtab").filter(':not(:animated)').animate({ scrollLeft: offset.toString() + 'px' });
} else if (106 * (index + 1) > $("#navtab").scrollLeft()) {
$("#navtab").filter(':not(:animated)').animate({ scrollLeft: '0px' });
}

}

},
infinite(done) {
this.getList(done);
},
refresh(done) {
this.p = 1;
this.getList(done);
},
getList(done) {
var postParams = {
p: this.p,
by: getModuleKeyword(this.nav_type),
};

AjaxSilentHttp('POST', 'm=index&a=dynamic', postParams, this, function(res, wself, success) {
if (success && isArrayType(res.list)) {
var newItems = [];
for (var i = 0; i < res.list.length; i++) {
newItems[i] = wself.getNewItem(res.list[i]);
}
if (wself.p === 1) {
wself.list = newItems;
} else {
wself.list = wself.list.concat(newItems);
}
if (typeof done === 'function') {
if (wself.p === 1 && res.list.length < 10) {
done(true);
} else if (res.list.length === 0) {
done(true);
} else {
done();
}
}
wself.p = wself.p + 1;

wself.saveCache(); 
}
});
},
getNewItem(item) {
if (this.nav_type === PlateType.Leads || this.nav_type === PlateType.Customer || this.nav_type === PlateType.Business || this.nav_type === PlateType.Contract) {
item.img = getImageUrl(item.img);
item.type = item.type.toString();
return item;
} else if (this.nav_type === PlateType.Contacts) {
return { top: '', left: '', right: '' };
} else if (this.nav_type === PlateType.Product) {
return { top: '', left: '', right: '' };
}
return { top: '', left: '', right: '' }
},
handelCache(type) {

if (typeof this.mDynamicList[getModuleKeyword(type)] === "undefined") {
this.nav_type = type;
this.list = [];
this.p = 1;
this.$nextTick(() => {
this.$refs.my_scroller.finishInfinite(false);
});
} else {
var item = this.mDynamicList[getModuleKeyword(type)];
this.nav_type = item.type;
this.list = item.list;
this.p = item.p;
this.$nextTick(() => {
this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, item.scroll, false);
if (item.loadingState === 2) {
this.$refs.my_scroller.finishInfinite(true);
}
});
}
},
saveCache() {

this.mDynamicList[getModuleKeyword(this.nav_type)] = {
type: this.nav_type, //平台类型与type相同 进行赋值
list: this.list,
p: this.p,
scroll: this.$refs.my_scroller.getPosition().top,
loadingState: this.$refs.my_scroller.loadingState
};

if (typeof this.mDynamicList !== "undefined") {
this.$store.commit('updateDynamicList', this.mDynamicList);
}
},
}
};




