import PlateType from '../../../js/enum.js'

export default {
  data() {
    return {
      title: [
        ['线索名称', '线索来源', '下次联系'],
        ['客户名称', '客户行业', '客户来源'],
        ['客户名称', '商机阶段', '商机金额'],
        ['客户名称', '商机阶段', '商机金额'],
        ['客户名称', '合同金额', '合同金额'],
        ['日程时间', '日程内容', '日程相关'],
        ['提醒时间', '提醒内容', '相关客户'],
        ['任务名称', '优先级', '相关项目'],
        ['审批类型', '审批金额', '审批内容']
      ]
    }
  },
  computed: {
    PlateType() {
      return PlateType;
    },
  },
  props: {
    item: {
      role_id: "",
      user_name: "",
      role_name: "",
      img: require('../../../assets/headimg.png'),
      content: "",
      dataa: "",
      datab: "",
      type: "",
      aname: "",
      id: "",
      create_time: ""
    }
  },
  mounted() {
    // console.log('type---' + this.item.type);
  },
  methods: {
    enterRelateDetail(item) {
      this.$router.push({
        name: 'maindetail',
        params: {
          id: this.item.id,
          type: this.item.type
        }
      });
    },
  }
}




